.cen{
  min-height: 700px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  row-gap: 80px;
  
}
.box{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 650px;
  width: 600px;
  
  font-size: 20pt;
  gap: 10px;
}

.text{
  border: 3px solid black;
  outline-color:  #cd7b7b;
  height: 655px;
  width: 630px;
  font-size: 17pt;
  resize: none;
  padding: 8px 15px;
  margin: 2px 0;
  gap: 10px;
  
}
.selected{
  box-shadow: 0px 8px 16px 0px rgba(234, 232, 232, 0.363);
 
}
ul {
  min-height: 20px;
  background-color:#282c34;
  display: flex;
  flex-wrap: wrap;
  font-size: 30px;

  gap: 10px;
  padding: 4px 20px;
  justify-content: space-between;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
 li h1 {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  align-items: center;
  cursor: none
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #282c34;
  min-width: 137px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 5;
  font-size: 20px;
  text-align: center;
  
}

.dropdown-content button {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #db6767;}

.dropdown:hover .dropdown-content {display: block;}

.dropdown:hover .dropbtn {background-color: #00000000;}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
